@charset "utf-8";
@font-face {
  font-family: "Code Light";
  src: url("/fonts/code-light.woff") format("woff");
  font-weight: 100;
  font-size: inherit;
  line-height: inherit;
  font-style: normal;
}
@font-face {
  font-family: "Code Bold";
  src: url("/fonts/code-bold.woff") format("woff");
  font-weight: 400;
  font-size: inherit;
  line-height: inherit;
  font-style: normal;
}
html,
body,
:root {
  width: 100%;
  height: 100%;
  background: #fff;
  font-family: Ubuntu, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-overflow-scrolling: touch;
}
::selection {
  color: #fff;
  background: rgba(214,109,77,0.988);
}
* {
  padding: 0;
  margin: 0;
  position: relative;
  box-sizing: border-box;
  text-rendering: optimizeLegibility;
}
*.flex {
  display: flex;
  align-items: center;
  justify-content: center;
}
*.noselect {
  user-select: none;
}
*.nodisplay {
  display: none;
}
*.container {
  width: 100%;
  height: 100%;
}
span.project {
  font-family: "Code Light", sans-serif;
  font-weight: 100;
}
span.beta {
  font-family: "Code Bold", sans-serif;
  font-weight: 100;
}
a:focus {
  box-shadow: inset 0 0 0 3px #d66d4d;
  border: none;
  outline: none;
}
img {
  user-drag: none;
  -webkit-user-drag: none;
  user-select: none;
}
input {
  transition: 0.2 all linear !important;
}
input:focus {
  border-color: #ddd !important;
}
*.yellow {
  color: #eddc96;
}
*.blue {
  color: #80baf9;
}
*.green {
  color: #a3eea0;
}
*.red {
  color: #f8867a;
}
*.white {
  color: #ddd;
}
section.sidebar {
  height: 100%;
  background: #1e2125;
  position: fixed;
  top: 0;
  left: 0;
  border-right: 1px solid #0e0f10;
  font-weight: 500;
  user-select: none;
  overflow-y: auto;
  overflow-x: hidden;
  -webkit-overflow-scrolling: touch;
  z-index: 2;
  transition: 0.05s all ease-out;
}
section.sidebar > .container {
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  overflow-x: hidden;
}
section.sidebar > .container::-webkit-scrollbar {
  width: 6px;
}
section.sidebar > .container::-webkit-scrollbar-thumb {
  background: #bbb;
}
section.sidebar > .container::-webkit-scrollbar-track {
  background: #3e4044;
}
section.sidebar.open {
  width: 250px;
}
section.sidebar.closed {
  display: flex;
  align-items: center;
  width: 50px;
  flex-direction: column;
}
section.sidebar.closed #heading {
  padding: 0;
  flex-direction: row;
  transform: rotate(-90deg);
  background: transparent;
  position: absolute;
  bottom: 50%;
  border: none;
  min-height: auto;
}
section.sidebar.closed #heading img {
  margin-right: 10px;
  height: 40px;
  width: 40px;
  margin-bottom: 0;
}
section.sidebar.closed #heading a {
  text-decoration: none;
}
section.sidebar.closed #heading a.image,
section.sidebar.closed #heading a.h1 {
  margin: 0;
}
section.sidebar.closed #sidebar-content,
section.sidebar.closed #footer {
  display: none;
}
section.sidebar > svg {
  fill: #ccc;
  position: fixed;
  top: 0;
  z-index: 1;
  cursor: pointer;
}
section.sidebar > svg#hamburger {
  height: 40px;
  margin-top: 8px;
}
section.sidebar > svg#cross {
  margin: 15px 0 0 15px;
}
#heading {
  margin: 0 auto;
  background: #161719;
  flex-direction: column;
  border-bottom: 1px solid #0e0f10;
  width: 100%;
  min-height: 190px;
}
#heading a {
  text-decoration: none !important;
}
#heading a.image {
  margin-bottom: 10px;
}
#heading a.h1 {
  margin-top: 10px;
}
#heading h1 {
  font-size: 30px;
  color: #fff;
}
#heading img {
  height: 80px;
  width: 80px;
  box-sizing: content-box;
}
#sidebar-content {
  width: 100%;
  flex: 1 0 auto;
}
.subject {
  background: rgba(255,255,255,0.133);
  height: 28px;
  justify-content: flex-start;
  cursor: pointer;
  border-bottom: 1px solid #0e0f10;
}
.subject p {
  color: #ddd;
  font-family: "Ubuntu";
  font-size: 15px;
  text-transform: uppercase;
}
.subject .triangle {
  margin: 0 6px;
}
#files {
  width: 100%;
  padding-bottom: 20px;
  overflow-y: auto;
  overflow-x: hidden;
  border-bottom: 1px solid #0e0f10;
}
#files .container {
  overflow: hidden;
}
#files .container ul {
  width: 100%;
}
#files .container ul li {
  width: 100%;
  color: #eee;
  cursor: pointer;
}
#files .container ul li a {
  width: auto;
  display: block;
  text-decoration: none;
  padding: 5px 0 5px 34px;
  font-size: 14px;
  letter-spacing: 0.5px;
}
#files .container ul li a.level2 {
  margin-left: 0px;
}
#files .container ul li a:focus {
  box-shadow: inset 0 0 0 2px #666 !important;
  border: none;
  outline: none;
}
#files .container ul li:hover {
  background: rgba(255,255,255,0.067);
}
#files .container ul li.level2 a {
  padding-left: 10px;
  margin-left: 34px;
  border-left: 1px solid;
}
#files .container ul li.level2.events a {
  border-left-color: rgba(163,238,160,0.561) !important;
}
#eventTab {
  margin-left: -14px !important;
}
#notifications {
  border-bottom: 1px solid #0e0f10;
}
#notifications ul a {
  text-decoration: none;
  width: 100%;
  height: 100%;
  display: block;
  padding: 10px 0;
}
#notifications ul li {
  color: #bbb;
  padding-left: 20px;
  font-size: 14px;
}
#countdown {
  border-bottom: 1px solid #0e0f10;
}
#countdown .timer {
  width: 100%;
  padding: 10px 0;
  justify-content: space-around;
}
#countdown .timer .flex {
  flex-direction: column;
}
#countdown .timer .flex p {
  font-size: 16px;
  color: #ddd;
  font-family: monospace;
}
#countdown .timer .flex h2 {
  font-size: 14px;
  color: #ddd;
  font-variant: small-caps;
}
#social {
  width: 100%;
}
#social ul {
  width: 100%;
  padding-bottom: 20px;
  padding: 5px 0;
}
#social ul li {
  width: 100%;
  height: 26px;
  cursor: pointer;
}
#social ul li a {
  width: 100%;
  text-decoration: none;
  padding-left: 20px;
  font-size: 14px;
  letter-spacing: 0.5px;
  justify-content: flex-start;
  display: inline-flex;
  height: 100%;
  color: inherit;
}
#social ul li a svg {
  height: 15px;
  margin: 0 10px 0 0;
}
#social ul li a svg path {
  fill: #ddd;
}
#social ul li a svg:nth-child(2) {
  margin: 0 15px;
}
#social ul li:nth-child(1) {
  color: #80baf9;
}
#social ul li:nth-child(1) svg path {
  fill: #80baf9;
}
#social ul li:nth-child(2) {
  color: #6cc644;
}
#social ul li:nth-child(2) svg path {
  fill: #6cc644;
}
#social ul li:nth-child(3) {
  color: #ef5b77;
}
#social ul li:nth-child(3) svg path {
  fill: #ef5b77;
}
#social ul li:hover {
  background: rgba(255,255,255,0.067);
}
.triangle {
  width: 0;
  height: 0;
  border-style: solid;
  display: inline-block;
  margin-right: 6px;
}
.triangle.closed {
  border-width: 4px 0 4px 8px;
  border-color: transparent transparent transparent #ddd;
}
.triangle.open {
  border-width: 0 0 8px 8px;
  border-color: transparent transparent #ddd transparent;
}
.triangle#aboutTriangle.open {
  border-bottom-color: #eddc96;
}
.triangle#aboutTriangle.closed {
  border-left-color: #eddc96;
}
.triangle#eventsTriangle.open {
  border-bottom-color: #a3eea0;
}
.triangle#eventsTriangle.closed {
  border-left-color: #a3eea0;
}
#footer {
  background: rgba(0,0,0,0.2);
  flex-direction: column;
  width: 100%;
  color: #666;
  text-transform: uppercase;
  font-size: 12px;
  min-height: 52px;
  border-top: 1px solid #0e0f10;
  margin-top: 30px;
  flex-shrink: 0;
}
#footer p:first-child {
  margin-bottom: 5px;
}
section.container {
  overflow-x: hidden;
}
section.main {
  height: 100%;
  margin-left: 250px;
  overflow-y: auto;
  overflow-x: hidden;
  align-items: flex-start;
  transition: 0.05s all ease-out;
  background: linear-gradient(150deg, #141e30, #243b55);
}
section.main canvas {
  position: fixed;
  opacity: 0.5;
  pointer-events: none;
}
section.main #ui-view {
  max-width: 1000px;
  width: 90%;
  min-height: 100vh;
  flex-direction: column;
  height: auto;
  align-self: flex-start;
}
section.main #ui-view >.container {
  margin: 5vh 0;
}
section.main #ui-view > img {
  width: 150px;
  margin: 4vh 0 0 0;
}
section.main #ui-view > h1 {
  font-size: 60px;
  margin: 4vh 0 0 0;
  color: #fff;
}
section.main #ui-view .ui-state h1 {
  font-size: 35px;
  font-weight: 100;
  padding-bottom: 10px;
  border-bottom: 1px solid #bbb;
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 4vh;
  color: #d66d4d;
}
section.main #ui-view .ui-state > p {
  margin: 0 0 20px 0;
}
section.main #ui-view .ui-state .section {
  margin-bottom: 20px;
  padding: 15px;
  border: 1px solid #000;
  border-radius: 6px;
  background: rgba(0,0,4,0.6);
}
section.main #ui-view .ui-state .section h2 {
  font-size: 21px;
  padding: 0 0 6px 0;
  font-weight: 300;
  text-transform: uppercase;
  color: #aaa;
}
section.main #ui-view > a {
  margin-bottom: 30px;
}
section.main p {
  font-size: 17px;
  line-height: 24px;
  letter-spacing: 0.5px;
  color: #ccc;
}
section.main a {
  padding-bottom: 3px;
  text-decoration: none;
  color: #fff;
  display: inline-block;
}
section.main a::after {
  content: '';
  width: 100%;
  height: 3px;
  background: rgba(214,109,77,0.467);
  position: absolute;
  transform: translate3d(-100%, 19px, 0);
  margin-top: 3px;
}
section.main a:hover::after {
  background: #d66d4d;
}
section.main h2.dates {
  color: #d66d4d;
  margin: 4vh 0 0 0;
  text-transform: uppercase;
  font-size: 27px;
  font-weight: bold;
  font-family: "Ubuntu Mono", 'Courier New', Courier, monospace;
}
section.main h2.dates span {
  font-size: 150%;
  padding: 0 10px;
}
section.main h2.dates span:nth-child(2) {
  border-right: 2px solid #ccc;
  border-left: 2px solid #ccc;
}
section.main .hr {
  height: 0px;
  width: 100%;
  margin: 4vh 0;
}
section.main a.button {
  height: 50px;
  width: 200px;
  background: rgba(214,109,77,0.2);
  margin: 50px 0 30px 0;
  color: #ddd;
  font-size: 22px;
  text-transform: uppercase;
  border: 3px solid #d66d4d;
  letter-spacing: 1.5px;
  transition: 0.2s all;
  cursor: pointer;
  padding: none;
  display: flex !important;
}
section.main a.button:hover {
  background: #d66d4d;
  color: #ddd;
}
section.main a.button:focus {
  border: 3px solid #d66d4d;
}
section.main a.button:after {
  content: none;
}
section.main .info {
  width: 100%;
  height: auto;
  align-items: flex-start;
  flex-wrap: wrap;
  flex-direction: column;
  max-height: 550px;
  justify-content: flex-start;
}
section.main .info .info-block {
  width: 45%;
  padding: 3vh 0;
}
section.main .info .info-block h1 {
  font-size: 27px;
  font-weight: 500;
  padding-bottom: 5px;
  border-bottom: 1px solid #777;
  color: #ddd;
}
section.main .info .info-block h2 {
  margin: 30px 0 0 0;
  font-size: 17px;
  font-weight: bold;
  text-transform: uppercase;
  padding: 0 0 6px 0;
}
section.main .info .info-block p {
  padding: 20px 0 0 0;
  color: #eee;
}
section.main .info .info-block #about-block {
  padding-bottom: 20px;
}
[ui-view] {
  position: absolute;
}
.anim-in-out.anim-in-setup.anim-fade,
.anim-in-out.anim-in-setup .anim-fade {
