#events .button {
  position: absolute;
  right: 15px;
  color: #ddd;
  font-size: 37px;
  top: 0;
  cursor: pointer;
  z-index: 3;
}
#events h2 {
  color: #ddd !important;
  text-transform: none !important;
  font-size: 25px !important;
  border-bottom: 1px solid rgba(102,102,102,0.4);
