#register {
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
}
#register h1 {
  width: 100%;
}
#register p {
  color: #ddd;
}
#register .section {
  width: 100% !important;
}
#register .section#stage1 {
  max-width: 500px !important;
  padding: 0 !important;
  align-self: center !important;
}
#register .section#stage1 h2 {
  margin: 20px 0;
}
#register .section#stage1 form {
  padding: 15px;
  border: 1px solid #ddd;
  border-top: none;
  border-bottom-left-radius: 6px;
  border-bottom-right-radius: 6px;
}
#register .section#stage1 .tabs {
  width: 100%;
  flex-direction: row !important;
}
#register .section#stage1 .tabs .tab {
  width: 50%;
  padding: 10px 0 10px 15px;
  font-size: 20px;
  text-transform: uppercase;
  font-weight: bold;
  cursor: pointer;
}
@media only screen and (max-width: 800px) {
  #register .section#stage1 .tabs .tab {
    font-size: 16px;
  }
}
#register .section#stage1 .tabs .tab.active {
  color: #d66d4d;
  border-top-right-radius: 6px;
